import React, { useState, useEffect } from 'react'
import minus from '../../../img/minus.svg'
export default React.memo(function Hotels() {
    const [data, setData] = useState()
    const [success, setSuccess] = useState(false)
    const [error, setError] = useState(false)
    const [description, setDescription] = useState(false)
    const [form, setForm] = useState(false)
    const [counter,setCounter] = useState(0)
    console.log(data);
    useEffect(() => {
        let xhr = new XMLHttpRequest();
        let response = []
        xhr.open('GET', 'http://82.146.63.178/getHotels');
        xhr.onreadystatechange = function () {
            if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
                response = JSON.parse(xhr.response)
                setData(response)
            }
        }
        xhr.send();
    }, [])
    function CreateHotel() {
        let xhr = new XMLHttpRequest();
        let response = []
        let formdata = new FormData(document.forms.create_tour)
        xhr.open('POST', 'http://82.146.63.178/createTours');
        xhr.onreadystatechange = function () {
            if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
                console.log(' ');
                setSuccess(true)
            }
            else {
                setError(true)
            }
        }
        xhr.send(formdata);
    }
    const changeDesc = e => {
        if (e.className === 'description active') {
            e.className = 'description'
            setDescription(false)
        } else {
            e.className = 'description active'
            setDescription(true)
        }
    }
    return (
        <div className='hotels'>
            <table>
                <tbody>
                    <tr>
                        <th rowSpan='2'>
                            id
                        </th>
                        <th rowSpan='2'>
                            
                        </th>
                        <th rowSpan='2'>
                            
                        </th>
                        <th rowSpan='2'>
                            
                        </th>
                        <th rowSpan='2'>
                            -(+)
                        </th>
                        <th rowSpan='2'>
                            -(+)
                        </th >
                        <th rowSpan='2'>
                            -(+)
                        </th>
                        <th rowSpan='2'>
                               
                        </th>
                        <th colSpan='3'>
                             
                        </th>
                        <th rowSpan='2'>
                            <button onClick={() => setForm(true)}>
                                 
                            </button>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                    </tr>
                    {data ? data.map((elem) => {
                        return <tr className='content'>
                            <td>
                                {elem.id}
                            </td>
                            <td>
                                {elem.name}
                            </td>
                            <td onClick={(e) => changeDesc(e.target)} className='description'>
                                {elem.description}
                            </td>
                            <td className='description' onClick={(e) => changeDesc(e.target)}>
                                {elem.services ? Object.keys(JSON.parse(elem.services)).map(e => {
                                    return JSON.parse(elem.services)[e] + ','
                                }
                                ) : ''}
                            </td>
                            <td className='description' onClick={(e) => changeDesc(e.target)}>
                                {elem.eco_room ? Object.keys(JSON.parse(elem.eco_room)).map(e => {
                                    return e === 'Price' ?
                                        <b>:{JSON.parse(elem.eco_room)[e]},</b> :
                                        JSON.parse(elem.eco_room)[e] + ','
                                }
                                ) : ''}
                            </td>
                            <td className='description' onClick={(e) => changeDesc(e.target)}>
                                {elem.standart_room ? Object.keys(JSON.parse(elem.standart_room)).map(e => {
                                    return e === 'Price' ?
                                        <b>:{JSON.parse(elem.standart_room)[e]},</b> :
                                        JSON.parse(elem.standart_room)[e] + ','
                                }
                                ) : ''}
                            </td>
                            <td className='description' onClick={(e) => changeDesc(e.target)}>
                                {elem.luxury_room ? Object.keys(JSON.parse(elem.luxury_room)).map(e => {
                                    return e === 'Price' ?
                                        <b> :{JSON.parse(elem.luxury_room)[e]}, </b> :
                                        JSON.parse(elem.luxury_room)[e] + ','
                                }
                                ) : ''}
                            </td>
                            <td className='description' onClick={(e) => changeDesc(e.target)}>
                                {elem.flight ? Object.keys(JSON.parse(elem.flight)).map(e => {
                                    return e + ' - ' + JSON.parse(elem.flight)[e].to + '-' + JSON.parse(elem.flight)[e].from + ';' + '\n'
                                }
                                ) : ''}
                            </td>
                            {elem.flight_price ? Object.keys(JSON.parse(elem.flight_price)).map(e => {
                                return <td>{JSON.parse(elem.flight_price)[e]} </td>
                            }
                            ) : ''}
                            <td onClick={() => console.log()}>
                                <img src={minus}></img>
                            </td>
                        </tr>
                    }) : ' '}
                </tbody>
            </table>
            {form ?
                <div className='create_tour_container'>
                    <form name='create_tour' className='create_tour'>
                        <input name='name' placeholder=' ' />
                        <textarea name='hotel' placeholder=' (<br>)  ' type='textarea' />
                        <input name='services' placeholder=' ( )' className='test1' />
                        <div onClick={e => {
                            let div = document.createElement('div')
                            let form = document.querySelector('.create_tour')
                            let add = document.querySelector('.button')
                            div.innerHTML = '<input type="text" placeholder="" name = "setkey'+counter+'" /> <input type="text" placeholder="" name = "value"/>'
                            setCounter(prev=>prev+1)
                            form.insertBefore(div, add)
                        }} className='button'></div>

                        {/* <input name='eco_room' placeholder='- ( ,  )' />
                        <input name='standart_room' placeholder='- ( ,  )' />
                        <input name='luxury_room' placeholder='- ( ,  )' />
                        <input name='date' placeholder=',  3 ( ,      -)' />
                        <input name='eco_price' placeholder='   ' />
                        <input name='standart_price' placeholder='   ' />
                        <input name='bisness_price' placeholder='   ' /> */}
                    </form>
                    <button onClick={() => CreateHotel()}>
                         
                    </button>
                </div> : ''}
        </div>
    )
})
